import React, { Component } from "react";
import "./HomePage.css";
import HeaderImage from "../images/header.png"; //TODO - rename
import ImageStudents from "../images/image-students.png";
import ImageWorkers from "../images/image-workers.png";
import ImageSurvivors from "../images/image-survivors.png";
import { render } from "@testing-library/react";

const Resources = require("../resources.json");

class HomePage extends React.Component {
  render() {
    return (
      <div className="page">
        <div className="container-title">
          <img className="image-title" src={HeaderImage} alt="people" />
          <h1 className="title">{Resources.HomePageTitle}</h1>
          <button className="button-vision">{Resources.ButtonVision}</button>
          <button className="button-impact">{Resources.ButtonImpact}</button>
          <button
            className="button-get-involved"
            onClick={this._onGetInvolvedClick}
          >
            {Resources.ButtonGetInvolved}
          </button>
          {/* Make button go to anchor point of get involved and then that button goes to link */}
        </div>
        <div className="container-vision">
          <h2 className="header-vision">{Resources.HeaderVision}</h2>
          <p className="paragraph-vision1">{Resources.ParagraphVision1}</p>
          <p className="paragraph-vision2">{Resources.ParagraphVision2}</p>
        </div>
        <div className="container-impact">
          <h2 className="header-impact">{Resources.HeaderImpact}</h2>
          <div className="container-students">
            <img
              className="image-students"
              src={ImageStudents}
              alt="students and youth"
            />
            <h3 className="header-students">{Resources.HeaderStudents}</h3>
            <p className="paragraph-students1">
              {Resources.ParagraphStudents1}
            </p>
            <p className="paragraph-students2">
              {Resources.ParagraphStudents2}
            </p>
          </div>
          <div className="container-workers">
            <img
              className="image-workers"
              src={ImageWorkers}
              alt="workers and youth"
            />
            <h3 className="header-workers">{Resources.HeaderWorkers}</h3>
            <p className="paragraph-workers1">{Resources.ParagraphWorkers1}</p>
            <p className="paragraph-workers2">{Resources.ParagraphWorkers2}</p>
          </div>
          <div className="container-survivors">
            <img
              className="image-survivors"
              src={ImageSurvivors}
              alt="survivors and youth"
            />
            <h3 className="header-survivors">{Resources.HeaderSurvivors}</h3>
            <p className="paragraph-survivors1">
              {Resources.ParagraphSurvivors1}
            </p>
            <p className="paragraph-survivors2">
              {Resources.ParagraphSurvivors2}
            </p>
          </div>
        </div>
        <div className="container-call-to-action">
          <h2 className="header-call-to-action">
            {Resources.HeaderCallToAction}
          </h2>
          <p className="paragraph-call-to-action">
            {Resources.ParagraphCallToAction}
          </p>
          <button
            className="button-get-involved-link"
            onClick={this._onGetInvolvedClickLink}
          >
            {Resources.ButtonGetInvolved}
          </button>
        </div>
      </div>
    );
  }

  private _onVisionClick = () => {};

  private _onImpactClick = () => {};

  private _onGetInvolvedClick = () => {
    document
      .getElementsByClassName("container-call-to-action")![0]
      .scrollIntoView();
  };

  private _onGetInvolvedClickLink = () => {
    window.open("https://tinyurl.com/wifiisalifeline", "_blank");
  };
}

export default HomePage;
